:root {
  font-family: 'Raleway', sans-serif;
}

body, html {
  height: 100%;
}

button {
  font-family: 'Raleway', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content {
  display: flex;
}


table {
  border: 2px solid gray;
  border-collapse: collapse;
}

tbody {
  overflow-y: scroll;
  display: block;
  height: 600px;
  width: 100%;
  position: relative
}

tr:hover {
  background-color: rgb(245, 245, 245);
  cursor: pointer;
}

th, td {
  border-collapse: collapse;
  padding: 5px 10px;
}

.td_text {
  font-size: 0.9rem;
}

th {
  text-align: left;
}


td li {
  list-style-type: none;
}

.header {
  position: sticky;
  top: 0;
}

th {
  background-color: black;
  color: white;
}

.data_table {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 20px 20px 0px 20px;
  overflow: auto;
}


.filters {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  max-width: 300px;
  border-right: 2px solid black;
  flex-grow: 1;
  overflow-y:  auto;
  height: 100vh;
}


.accordion {
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: center;
  border: none;
  outline: none;
  transition: 0.4s;
  display: flex;
  font-size: 1.2rem;
  font-weight: 700;
}

.accordion > span {
  margin-left: auto;
}

.accordion.type {
  background-color: rgb(224, 128, 93);
  color: white;
  border-bottom: 3px solid rgb(243, 160, 130);
}

.accordion.spec {
  background-color: cornflowerblue;
  color: white;
  border-bottom: 3px solid rgb(144, 181, 250);
}

.accordion.prop {
  background-color: darkcyan;
  color: white;
  border-bottom: 3px solid rgb(50, 175, 175);
}

.accordion.sub{
  color: rgb(255, 255, 255);
  font-size: 1rem;
  background-color: black;
  border-bottom: 3px solid rgb(68, 68, 68);
}

.accordion.sub.child {
 background-color: rgb(223, 223, 223);
 color: rgb(0, 0, 0);
 border-bottom: 3px solid rgb(179, 179, 179);
 text-align: left;
}

#type-btn {

}

#search-input {
  font-size: 1.3rem;
  padding: 5px;
  margin: 0px 10px 10px 10px;
  width: 100%;
}

.search-label {
  display: flex;
  align-items: center;
}

.item_name {
  display: flex;
  align-items: center;
  gap: 20px;
  font-size: 0.9rem;
}

.item_extra_damage {
  display: flex;
  align-items: center;
  justify-content: center;
}

.item_text {
  color:#337ab7;
  text-decoration: none;
}

.item_text:hover {
  text-decoration: underline;
}

.panel {
  background-color: white;
}

.filter-btn {
  cursor: pointer;
  padding: 10px;
  width: 100%;
  border: none;
  font-size: 16px;
  background-color: white;
  border-bottom: 1px solid gray;
}

.active-btn {
  background-color: firebrick;
  color: white;
}


.section.pages {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
  
.section.pages  div {
  display: flex;
  align-items: center;
  justify-content: center;
  }
  
  .section.pages  .main-pg {
  width: 30px;
  }
  
  .section.pages button {
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  height: 30px;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0;
  }
  
  #current-pg {
  outline: 1px solid gray;
  }
  
  .section.pages button:hover {
  transform: scale(1.2);
  }

  .radiant {
    color: #ccaa00;
  }

  .poison {
    color: #44bb00
  }

  .lightning {
    color: #3366cc
  }

  .acid {
    color: #80b000
  }

  .necrotic {
    color: #40b050
  }

  .fire {
    color: #ee5500;
  }

  .cold {
    color: #3399cc
  }

  .psychic {
    color: #cc77aa;
  }

  .item_special {
    text-align: left;
    font-size: 0.9rem;
  }

  #legend_label {
    font-size: 12px;
    margin: 5px;
    text-align: left;
  }

  .footer {
    margin-top: auto;
    font-size: 0.9rem;
    text-align: center;
  }